section{
    position: relative;
	z-index: 1;
}
.section-head{
	margin-bottom:30px;
	
	@include respond('phone-land'){
		margin-bottom:20px;
	}
	h1, h2, h3, h4, h5, h6{
		font-family: var(--font-family-title);
	}
	h2.title{
		font-weight: 600;
		font-size: 50px;
		line-height: 1.2;
		@include respond('laptop'){
			font-size:52px;
		}
		@include respond('tab-land'){
			font-size:42px;
		}
		@include respond('phone-land'){
			font-size:32px;
		}
		@include respond('phone'){
			font-size:32px;
		}
	}
	
	h3.title{
		font-size: 50px;
		font-weight:600;
		line-height: 1.2;
		@include respond('laptop'){
			font-size:42px;
		}
		@include respond('tab-land'){
			font-size:32px;
		}
		@include respond('phone'){
			font-size:28px;
		}
	}
	h4.title{
		font-size: 40px;
		font-weight:600;
		line-height: 1.2;
		margin-bottom: 5px;
		@include respond('laptop'){
			font-size:36px;
		}
		@include respond('phone'){
			font-size:30px;
		}
		
	}
	h6{	
		line-height: 1.5;
		color:var(--primary);
		font-weight:500;	
	}
	&.text-center p{
		max-width:753px;
		margin-left:auto;
		margin-right:auto;
		margin-bottom:0;
	}
	&.m-b30{
		margin-bottom:30px;
	}
	
	&.style-2{
		.title{
			font-size: 60px;
			line-height: 1.2;
			margin-bottom: 15px;
			
			@include respond('wide-desktop'){
				font-size: 48px;
			}
			@include custommq($max:1199px){
				font-size: 40px;
			}
			@include custommq($max:991px){
				br{
					display:none;
				}
			}
			@include custommq($max:767px){
				font-size: 32px;
			}
			@include respond('phone'){
				font-size: 28px;
			}
		}
		.sub-title{
			text-transform: unset;
			font-size: 22px;
			font-weight: 400;
			@include respond('wide-desktop'){
				font-size: 18px;
			}
			@include respond('tab-land'){
				font-size: 16px;
			}
			&.text-primary{
				@at-root .theme-dark &{
					color:#52E8D6 !important;
				}
			}
			&.text-dark{
				@at-root .theme-dark &{
					color:#fff !important;
				}
			}
		}
	}
}

