#pointer-dot {
  left: -4px;
  top: -4px;
  width: 12px;
  height: 12px;
  position: fixed;
  border-radius: 10px;
  z-index: 999998;
  pointer-events: none;
  transition: border-color 0.5s;
  background: #333;
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.2); }

#pointer-ring {
  left: 0;
  top: 0;
  width: 25px;
  height: 25px;
  display: block;
  border: 1px solid var(--primary) !important;
  position: fixed;
  border-radius: 100px;
  z-index: 999999;
  pointer-events: none;
  transition: width 0.3s, height 0.3s; }

#pointer-ring.active {
  width: 50px;
  height: 50px;
  opacity: 0.5; }

#pointer-ring.active + #pointer-dot {
  opacity: 0; }

#loading-area {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 999999999;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 80px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center; }
  #loading-area .loader {
    width: 100px;
    height: 100px;
    margin: 40px auto 0;
    position: relative;
    animation: animate 1s infinite; }
    #loading-area .loader .ball {
      width: 40%;
      height: 40%;
      border-radius: 50%;
      position: absolute;
      animation: speed 2s infinite ease-in-out; }
    #loading-area .loader .one {
      background: #afc531;
      animation-delay: 1.5s; }
    #loading-area .loader .two {
      background: #a11cc7;
      animation-delay: 1s; }
    #loading-area .loader .three {
      background: #058fd4;
      animation-delay: 0.5s; }
    #loading-area .loader .four {
      background: #f05151; }

@keyframes speed {
  0% {
    border-radius: 50%;
    transform: translate(0%); }
  25% {
    border-radius: 0;
    transform: translate(150%) scale(0.5); }
  50% {
    border-radius: 50%;
    transform: translate(150%, 150%); }
  75% {
    border-radius: 0;
    transform: translate(0, 150%) scale(0.5); } }

